.contact {
    margin: sz(40) auto sz(60);
    &__text1 {
        padding-bottom: 15px;
        font-weight: 700;
        letter-spacing: 1px;
        font-size: 24px;
        display: inline-block;
        margin: 0;
    }
    &__btn {
        width: sz(155);
        height: sz(40);
        margin: sz(12) auto 0;
        font-size: sz(14);
        i {
            margin-right: sz(8);
            margin-bottom: sz(2px);
        }
    }
    input,
    textarea {
        margin: sz(12) 0 sz(8);
        height: auto;
    }
    &__content {
        color: #999;
        margin-top: 10px;
    }
}

@media (min-width:sz(768)) {
    .contact {
        &__group {
            display: flex;
            justify-content: space-between;
        }
        &__group-inner {
            width: 48%;
        }
    }
}