.profile-wapper {
    background: #00b9f2;
    width: 100%;
    margin-bottom: 20px;
}
.profile-nav {
	color: #fff;
    display: flex;
    align-items: center;
    text-transform: capitalize;
    height: 85px;
    &__head {
    	display: inline-block;
	    font-size: 40px;
	    line-height: 0;
	    margin-left: 15px;
	    margin-right: 25px;
	    font-weight: 100;
    }
    ul {
    	float: none;
	    width: 65%;
	    display: inline-flex;
	    font-size: 16px;
	    font-weight: 100;
	    line-height: 0;
	    li {
	    	display: flex;
		    justify-content: center;
		    width: auto;
		    padding: 10px 20px;

		    a {
		    	display: inline-block;
			    color: #fff;
			    padding: 0 0 10px;
			    &:hover  {
		    		border-bottom: 1px solid #fff;
				    background-color: transparent;
				    outline: 0;
		    	}
		    }
		    .active {
		    	border-bottom: 1px solid #fff;
			    background-color: transparent;
			    font-weight: 600;
			    outline: 0;
		    }
	    }
    }
}

.profile-sibar {
	background-color: #fff;
    text-align: center;
    padding: 40px 20px;
    margin-bottom: 10px;
    margin-bottom: 13px;
    border: 1px solid #e5e5e5;

    &__name {
    	font-size: 24px;
	    font-weight: 600;
	    margin-bottom: 5px;
	    margin-top: 10px;
	    color: #555;
    }
    &__img {
    	padding: 0;
	    background: #fff;
	    width: 95px;
	    margin: auto;
	    margin-bottom: 15px;
	    height: 95px;
	    display: flex;
	    justify-content: center;
	    align-items: center;
	    img {
	    	border-radius: 50%;
	    	border: 1px #E5E5E5 solid;
	    }
    }
    ul {
    	margin-bottom: 20px;
    }
    &__btn {
    	padding: 8px 20px;
    	color: $orange!important;
    	border: 1px solid $orange;
    	border-radius: 3px;
    	background: #fff;
    	font-size: 15px;
    	font-weight: 600;
    	&:hover {
    		background: $orange;
    		color: #fff!important;
    	}
    }
}

.profile {
	&__box {
		background-color: #fff;
    	border: 1px #e5e5e5 solid;
    	margin-bottom: 40px;
	}
	&__head {
		font-size: 20px;
	    font-weight: 500;
	    margin-left: 25px;
	    margin-top: 30px;
	    margin-bottom: 15px;
	    word-spacing: 2px
	}
	&__content {
		margin: 10px 25px;
		padding: 0 0 15px;
	}
	&__jobs {
		margin: 5px 0;
	    border-top: 1px solid rgba(229,229,229,.9);
	    padding-top: 15px;
	    padding-bottom: 10px;
	}
	&__title {
		font-size: 17px;
	    font-weight: 600;
	    margin-bottom: 2px;
	    color: #555;
	    display: inline-block;
	    white-space: nowrap;
	    overflow: hidden;
	    text-overflow: ellipsis;
	    max-width: 100%;
	    vertical-align: middle;
	    &:hover {
	    	color: $orange;
	    }
	}
	&__desc {
		display: inline-block;
	    white-space: nowrap;
	    overflow: hidden;
	    text-overflow: ellipsis;
	    max-width: 100%;
	    vertical-align: middle;
	    text-transform: uppercase;
	    font-size: 13px;
	    color: #999;
	}
	.row {
		margin-left: 5px;
		margin-right: 5px;
		.col-lg-4,
		.col-lg-8,
		.col-3,
		.col-9 {
			padding-left: 5px;
			padding-right: 5px;
		}
	}
}
