.company-result {
	background-color: #fff;
    padding: 15px 0 25px;
    border-top: 1px solid rgba(230,230,230,.5);
    border-bottom: 1px solid rgba(230,230,230,.5);
    font-size: 14px;
    color: #999;
    &__headding {
    	margin-bottom: 20px;
    }
    &__text {
    	font-weight: 700;
    	padding-right: 5px;
    	color: #000;
    }
    &__checkbox {
    	input {
    		width: 15px;
    		height: 15px;
    	}
    }
    &__link {
    	text-transform: uppercase;
    	color: #00b9f2;
    	padding-left: 7px;
    	& + i {
    		padding-left: 5px;
    		font-size: 20px;
    	}
    }
    &__mb {
    	margin-bottom: 10px;
    }
}