.company-search {
	padding-bottom: 10px;
    background: #f9f9f9;
    border: 1px solid rgba(230,230,230,.8);
    box-shadow: 1px 1px 1px #f1f1f1;
	&__head {
		font-size: 40px;
	    font-weight: 400;
	    text-transform: uppercase;
	    margin: 25px 0 0;
	    color: $orange;
	}
	.search-widget__dropdown {
		color: #444;
	    font-size: 13.5px;
	    display: flex;
	    justify-content: space-between;
	    align-items: center;
	    border: 1px solid rgba(230,230,230,.8);
	    border-radius: 0;
	    input {
	    	width: 100%;
	    }
	}
	.search-widget__input {
		input {
			padding: 10px 15px;
			border: 1px solid rgba(230,230,230,.8);
			border-radius: 0;
		}
	}
	.search-widget__btn {
		border-radius: 0;
	}
	.row {
		margin-left: -5px;
		margin-right: -5px;
		.search-col {
			padding-left: 5px;
			padding-right: 5px;
		}
	}
}
.company-list {
	background-color: #fff;
    margin-bottom: 10px;
    padding: 15px 0 25px;
    border-top: 1px solid rgba(230,230,230,.5);
    border-bottom: 1px solid rgba(230,230,230,.5);
    &:last-child {
    	margin-bottom: 60px;
    }
    &__head {
    	font-size: 30px;
    	font-weight: 300;
    	margin-bottom: 10px;
    	color: #555;
    }
    &__item {
    	margin-bottom: 30px;
	    border: 1px solid rgba(230,230,230,.8);
	    box-shadow: 1px 1px 1px #f1f1f1;
	    overflow: hidden;
    }
    &__info {
    	padding: 20px;
    }
    &__img {
    	position: relative;
    }
    &__opening {
    	background-color: #00b9f2;
	    display: inline-block;
	    padding: 5px 35px;
	    color: #fff;
	    font-weight: 500;
	    text-transform: uppercase;
	    position: absolute;
	    right: -35px;
	    top: 25px;
	    z-index: 22;
	    -ms-transform: rotate(45deg);
	    -webkit-transform: rotate(45deg);
	    transform: rotate(45deg);
	    font-size: 11px;
    }
    &__content {
    	width: -moz-calc(100% - 31px);
	    width: -webkit-calc(100% - 31px);
	    width: -o-calc(100% - 31px);
	    width: calc(100% - 31px);
    }
    &__title {
    	display: block;
    	text-overflow: ellipsis;
    	overflow: hidden!important;
    	white-space: nowrap;
    	color: rgba(51,51,51,.9);
    	cursor: pointer;
    	font-size: 25px;
    	font-weight: 400;
    	&:hover {
    		color: $orange;
    	}
    }
    &__text {
    	text-overflow: ellipsis;
	    overflow: hidden;
	    white-space: nowrap;
	    font-size: 13px;
	    line-height: 20px;
	    color: #454545;
	    font-weight: 300;
    }
    &__heart {
    	font-size: 13px;
	    line-height: 20px;
	    color: #999;
	    font-weight: 300;
	    i {
	    	font-size: 26px;
	    	margin: 5px 0;
	    }
    }
    .owl-theme .owl-nav {
    	display: none;
    }
    .owl-dot.active span {
    	width: 15px;
    	height: 15px;
	    background: #00b9f2!important;
	    opacity: 1;
    }
    .owl-dots {
    	display: flex;
    	justify-content: center;
    	align-items: center;
    }
}

@media (max-width: 991px) {
	.company-search {
		background: #333;
		padding: 35px 30px 30px;
		color: #fff;
		&__head {
			font-size: 22px;
			text-align: center;
			text-transform: none;
    		padding-bottom: 5px;
		}
	}
}