.company-header {
	background-color: rgb(0, 185, 242);
	display: flex;
	justify-content: center;
	align-items: center;
	height: 55px;
	position: fixed;
	width: 100%;
	z-index: 9999;
	&__nav li.active a {
		background-color: rgba(255,255,255, 0.2);
	    color: #fff;
	}
	&__link {
		padding: 17px 25px;
	    cursor: pointer;
	    text-transform: capitalize;
	    color: #fff;
	    font-size: 16px;
	    display: flex;
	    align-items: center;
	    justify-content: center;
	    i {
	    	padding-right: 7px;
	    }
	    &:hover {
	    	background-color: rgba(255,255,255, 0.2);
	    	color: #fff;
	    }
	}
}

.company-banner {
	display: inline-block;
    width: 100%;
    background-color: #fff;
    border-top: 1px solid #337ab7;
    border-bottom: 1px solid #337ab7;
    padding: 15px 0;
    &__img {
    	display: flex;
    	justify-content: center;
    	text-align: center;
    	img {
    		width: 140px;
    		height: 70px;
    		margin-top: 5px;
    		margin-right: 10px;
    	}
    }
    &__info {
    	padding-left: 10px;
    	font-size: 14px;
    }
    &__title {
    	font-size: 24px;
	    font-weight: 400;
	    padding: 0 0 5px;
    }
    &__light {
    	margin: 0 10px;
    }
    &__text {
    	color: #555;
    }
    &__link {
    	color: #00b9f2;
    }
    &__share {
    	.btn-red {
    		background: #00b9f2;
    		color: #fff;
    		padding: 10px 20px;
    		border-radius: 3px;
    		border: none;
    		width: 100%;
    		&:hover {
    			opacity: 0.8;
    		}
    		span {
    			padding-left: 5px;
    		}
    	}
    }
}
.company-wapper {
	background: #e3ebec;
}
.company-title {
	font-size: 35px;
    font-weight: 300;
    text-transform: capitalize;
    padding: 30px 0;
    margin-top: 20px;
    margin-bottom: 10px;
    text-align: center;
    span {
    	width: 80px;
	    height: 2px;
	    display: block;
	    margin: 15px auto auto;
	    background-color: #00b9f2;
    }
}
.company-jobs {
	width: 100%;
    padding: 50px 25px;
    background-color: #fff;
    border: 1px solid #e9e9e9;
    margin-bottom: 0;
    &__head {
    	font-size: 20px;
	    text-transform: capitalize;
	    margin: 0 0 20px;
	    padding: 0 0 15px;
	    border-bottom: 2px solid #00b9f2;
	    width: auto;
	    display: inline-block;
    }
    &__item {
    	padding: 5px 0 20px;
	    margin-top: 10px;
	    border-bottom: 1px solid #e2e2e2;
	    width: 100%;
	    &:last-child {
	    	border: none;
	    }
    }
    &__title {
    	font-size: 16px;
	    text-transform: capitalize;
	    font-weight: 700;
	    padding: 0 0 5px;
	    a {
	    	color: #333;
	    	&:hover {
	    		color: #00b9f2;
	    	}
	    }
    }
    &__meta {
    	color: #999;
    	text-transform: uppercase;
    	font-size: 13px;
    }
    &__text {
    	padding-right: 15px;
    	i {
    		padding-right: 5px;
    	}
    }
    &__btn {
    	display: inline-block;
	    width: 100%;
	    max-width: 150px;
	    border: 1px solid #999;
	    height: 42px;
	    line-height: 40px;
	    text-align: center;
	    font-size: 16px;
	    color: #333;
	    cursor: pointer;
	    transition: background-color 0.5s ease;
	    background: #fff;
	    &:hover {
	    	background: #00b9f2;
	    	border: 1px solid #00b9f2;
	    	color: #fff;
	    }
    }
    &__btn-bt {
    	padding: 20px 0;
    	text-align: center;
    }
}

.company-intro {
	margin-bottom: 40px;
	background: #fff;
	padding: 20px;
	font-size: 14px;
	border-top: 2px solid #00b9f2;
	img {
		margin-bottom: 20px;
	}
	&__title {
		font-size: 18px;
	    text-transform: uppercase;
	    color: #00b9f2;
	    font-family: Roboto, Arial, sans-serif;
	    padding: 0;
	    margin: 0 0 15px;
	    font-weight: 700;
	}
}

@media (max-width: 991px) {
	.company-banner__content {
		margin-bottom: 20px;
		text-align: center;
	}
}

@media (max-width: 767px) {
	.company-title {
		font-size: 22px;
	}
	.company-header {
		&__nav {
			li {
				width: 25%;
			}
		}
	}
}